.main_users {
  margin: 0 0 auto 0;
  width: 100%;
}
.comeback {
  margin-right: auto;
  font-size: calc(var(--index) * 4);
  cursor: pointer;
  width: max-content;
}

.users_title {
  text-align: center;
  font-size: calc(var(--index) * 2.5);
}
.users_container {
  margin: calc(var(--index) * 2.5) 0 0 0;
  display: flex;
  flex-direction: column;
  gap: calc(var(--index) * 0.1);
}
.user {
  display: flex;
  flex-direction: column;
  gap: 1vh;
  padding: calc(var(--index) * 0.01) 0;
  border-bottom: 1px solid #808080;
}
.one_user {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.one_user h2 {
  font-size: calc(var(--index) * 1.5);
}
.one_user span:nth-child(2) {
  font-size: calc(var(--index) * 0.5);
  color: #808080;
}
.one_user div:nth-child(3) {
  display: flex;
  align-items: center;
  gap: 2vw;
}
.one_user div:nth-child(3) span {
  font-size: calc(var(--index) * 1);
}
.one_user div:nth-child(3) img {
  max-width: calc(var(--index) * 3);
  cursor: pointer;
}
.tariffs {
  margin-top: 1vh;
  display: flex;
  gap: 3vw;
  align-items: center;
}
.tariffs span:nth-child(1) {
  font-weight: 700;
  font-size: calc(var(--index) * 0.5);
}
.tariffs span {
  font-weight: 100;
  font-size: calc(var(--index) * 0.1);
  padding: 8px 25px;
  border-radius: 10em;
  cursor: pointer;
}
