.head {
  margin: 0 auto 20px auto;
  text-align: center;
  font-size: calc(var(--index) * 6);
  font-weight: 900;
}
.head_h3 {
  font-size: calc(var(--index) * 5);
  text-align: center;
  margin-bottom: 20px;
}
.main {
  display: flex;
  width: 100%;
  flex-direction: column;
  gap: calc(var(--index) * 1.1);
}

.mainChildren {
  position: relative;
  padding: 25px 40px 25px 25px;
  border-radius: 10px;
  cursor: pointer;
  font-size: calc(var(--index) * 2.5);
  transition: 0.5s;
}
.mainChildrenText {
  border-bottom: 1px solid #00457c;
  padding: 0;
  padding-bottom: 10px;
  padding-left: 20px;
  max-width: 100%;
  display: flex;
  justify-content: flex-start;
}

.clouse {
  position: absolute;
  display: inline-block;
  height: max-content;
  top: 0;
  bottom: 0;
  right: calc(var(--index) * 1.05);
  font-size: calc(var(--index) * 1.6);
  font-weight: 400;
  margin: auto 0;
  transition: 0.5s;
}
.clouse:hover {
  transform: scale(1.1, 1.1);
  text-shadow: 0 0 5px white;
  color: black;
  font-weight: bold;
  transition: 0.5s;
}
@media (max-width: 850px) {
  .head {
    font-size: calc(var(--index) * 2.5);
  }
  .head_h3 {
    font-size: calc(var(--index) * 2.5);
  }
  .mainChildren {
    font-size: calc(var(--index) * 1.5);
  }
}
@media (max-width: 520px) {
  .head_h3 {
    max-width: 300px;
    margin: 0 auto 20px auto;
  }
}
