.App {
  min-width: 100vw;
  min-height: 93vh;
  max-height: max-content;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

:root {
  --index: 1vw + 1vh;
}
.adminBackground,
.appBackground {
  position: fixed;
  z-index: -1;
  top: 0;
  bottom: 0;
  min-width: 100vw;
  height: 100vh;
}

.mainContainer {
  padding: calc(var(--index) * 3) calc(var(--index) * 3);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.containerQuestions {
  display: flex;
  justify-content: center;
  margin: auto 0 40px 0;
  min-height: 30vh;
  min-width: 50vw;
}
.nextOrPrevConsole {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nextOrPrevConsole span {
  cursor: pointer;
  font-size: 30px;
  transition: 0.5s;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 7px;
  border: 1px solid white;
  border-radius: 50%;
}

.pagination {
  display: flex;
  gap: 5px;
}
.pagination div {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1px solid white;
  cursor: pointer;
}
.poweredByMero {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  background: #252525;
  margin-top: 20px;
  border-radius: 10px;
  padding: 20px 30px;
  overflow: hidden;
  opacity: 0.8;
}
.finishButton {
  padding: calc(var(--index)) calc(var(--index) * 3);
  border: none;
  width: 50%;
  margin: 20px auto 0;
  cursor: pointer;
  font-size: calc(var(--index) * 1.7);
  font-weight: 700;
}

.ask_lector {
  cursor: pointer;
  position: fixed;
  font-size: calc(var(--index) * 0.5);
  padding: calc(var(--index) * 0.5) calc(var(--index) * 2);
  background-color: black;
  border-radius: calc(var(--index) * 0.5);
  z-index: 3;
}

.label {
  position: static;
  width: max-content;
  height: max-content;
  margin: auto 4% 0 auto;
  display: block;
  padding: 10px 0;
}
.label img {
  width: calc(var(--index)*30);
}

@media (max-width: 1400px) {
  .appBackground {
    width: auto;
  }
}
@media (max-width: 1400px) {
  .appBackground {
    width: auto;
  }
  .label img {
    width: calc(var(--index)*20);
  }
}
@media(max-width:500px){
  .ask_lector {
    font-size: calc(var(--index) * 2);
    padding: calc(var(--index)) calc(var(--index) * 3);
    border-radius: calc(var(--index));
  }
  .finishButton {
    width: 70%;
  }
}
@media (max-width: 450px) {
  
  .poweredByMero {
    gap: 5px;
  }
  .mainContainer {
    padding: calc(var(--index) * 3) calc(var(--index) * 2);
  }
  .poweredByMero span {
    font-size: 14px;
    max-width: 100px;
  }
  .poweredByMero img {
    width: 100px;
  }
  .pagination {
    gap: 3px;
  }
  .pagination div {
    width: 7px;
    height: 7px;
  }
}
